import styled, { keyframes } from "styled-components";

const loading = keyframes`
  from {
    background-position: 0 0;
    /* rotate: 0; */
  }

  to {
    background-position: 100% 100%;
    /* rotate: 360deg; */
  }
`;

const Form = styled.form`
  border: 5px solid white;
  padding: 20px;
  font-size: 1.5rem;
  line-height: 1.5;
  font-weight: 600;
  position: fixed;
  width: 100vw;
  left: 0;
  bottom: 0;
  background: black;

  label {
    display: block;
    margin-bottom: 1rem;
    color: white;
  }
  input,
  textarea,
  select {
    width: 100%;
    padding: 0.5rem;
    font-size: 1rem;
    border: 1px solid black;
    &:focus {
      outline: 0;
      border-color: ${props => props.theme.red};
    }
  }
  button,
  input[type="submit"] {
    width: auto;
    background: red;
    color: white;
    border: 0;
    font-size: 2rem;
    font-weight: 600;
    padding: 0.5rem 1.2rem;
  }
  .wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    label {
      width: 70%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    input {
      width: 100%;
    }
    button {
      width: 150px;
    }
  }
  fieldset {
    border: 0;
    padding: 0;
    display: flex;

    &[disabled] {
      opacity: 0.5;
    }

    &[aria-busy="true"]::before {
      background-size: 50% auto;
      animation: ${loading} 0.5s linear infinite;
    }
  }
`;

export default Form;
